<template src="./index.html"></template>

<script>
    export default {
        name: 'index',
        methods:{
            checkRate(event){
                let key = event.target.dataset.num ; //取執行次數
                const star = document.querySelectorAll('.fa-star');
                const fas = document.querySelectorAll('.fas');

                if(fas.length < key){
                    for(var j = 0; j<key;j++){
                        star[j].classList = 'fas fa-star';
                    }
                }else{
                    for(var i = 4; i >= key; i--){  //fas減相
                        star[i].classList = 'far fa-star';
                    }
                }
            }
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" src="./index.scss"></style>
